@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Display";

.passwordSection {
    #{Text.$selector}.title {
        @include Margin.set($left: beta, $bottom: beta);
        @include Flex.set($align: start);
        @include Color.set($fg: "gray-dark");
    }
    .alert {
        @include Flex.set($align: center);

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
        }

        .alertIcon {
            @include Flex.set($shrink: none);
            @include Margin.set($right: alpha);
        }
    }
    .row {
        @include Flex.set($justify: between);
        @include Margin.set($top: beta);
    }

    .column {
        @include Flex.set($dir: column);
        @include Margin.set($top: beta);
    }

    .label {
        @include Color.set($fg: "dark-gray");
        @include Font.set($family: heading, $weight: bold, $transform: capitalize);
        @include Flex.set($align: start);
    }

    .emailLabel {
        @include Color.set($fg: "dark-gray");
        @include Font.set($family: heading, $weight: bold);
        @include Flex.set($align: start);
    }

    .info {
        @include Color.set($fg: "black");
        @include Font.set(
            $family: heading,
            $weight: bold,
            $size: gamma,
            $transform: uppercase,
            $align: right
        );
        @include Flex.set($justify: end);
    }

    .bigLabel {
        @include Color.set($fg: "primary");
        @include Font.set($family: heading, $weight: bold, $size: iota);
    }

    .saveButton {
        @include Margin.set($top: omega);
    }

    .alignInputs {
        @include Margin.set(auto);
    }

    #{Form.$selector} {
        #{Button.$selector} {
            @include Margin.set($top: beta);
        }
        #{Input.$selector}[data-name="InputField"] {
            @include Margin.set($bottom: beta);
        }
        width: Layout.get-max-width(alpha);
    }

    @include screen.lg {
        @include Margin.set($left: auto, $right: auto, $bottom: auto);
        width: Layout.get-width(iota);
    }
}
